export function InvokePublication(id, userId) {
    if (id==null || userId==null){
        id = 0
        userId=0
    } else {
        id=id
        userId=userId
    }
    
    const responce = fetch(
      `https://api.codetabs.com/v1/proxy/?quest=https://ecocomplect.1forma.ru/app/v1.2/api/publications/action/LibHeaders?id=${id}&user=${userId}`
    ).then((responce) => responce.json());
  
    return responce;
  }