import React, { useEffect, useState } from "react";
import { InvokePublication } from "../Services/ServicesFunctions";
import { useRequest } from "ahooks";
import {
  Container,
  Navbar,
  Nav,
  Form,
  FormControl,
  Button,
} from "react-bootstrap";

const MainNavMenu = (props) => {
  const { data, error, run, loading } = useRequest(InvokePublication, {
    manual: true,
  });

  useEffect(() => {
    run(0, 0);
    return () => console.log("MainNavMenu unmount");
  }, []);
  if (data) {
    return (
      <Navbar variant="pills" bg="white" expand="sm" sticky="top" className="justify-content-between">
        <Container fluid>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav variant="pills" className="me-auto">
              {data.map((menuItem) => (
                <MainNavMenuItem
                  key={menuItem.Id}
                  MenuItem={menuItem}
                  SetSideNavMenuSelectedId={props.setSideNavMenuSelectedId}
                />
              ))}
            </Nav>
            <Form className="d-flex">
              <FormControl
                type="search"
                placeholder="Поиск"
                className="me-2"
                aria-label="Поиск"
              />
              <Button variant="outline-success">Поиск</Button>
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    );
  }
};

function MainNavMenuItem(props) {
  if (props.MenuItem.HeadersCount == 0) {
    return (
      <Nav.Item>
        <Nav.Link
          disabled={true}
          eventKey={props.MenuItem.Id}
          onClick={() => props.SetSideNavMenuSelectedId(props.MenuItem.Id)}
        >
          {props.MenuItem.TaskName}
        </Nav.Link>
      </Nav.Item>
    );
  } else {
    return (
      <Nav.Item>
        <Nav.Link
          disabled={false}
          eventKey={props.MenuItem.Id}
          onClick={() => props.SetSideNavMenuSelectedId(props.MenuItem.Id)}
        >
          {props.MenuItem.TaskName}
        </Nav.Link>
      </Nav.Item>
    );
  }
}

export default MainNavMenu;
