import React, { useState } from "react";
import { InvokePublication } from "../Services/ServicesFunctions";
import { useRequest } from "ahooks";
import { Col, Container, Row } from "react-bootstrap";
import TestComp from "./TestComp";
import MainNavMenu from "./MainNavMenu";
import SideNavMenu from "./SideNavMenu";
import ContentBlock from "./ContentBlock";

const Pwa = () => {
  const { data, error, run, loading } = useRequest(InvokePublication, {
    manual: true,
  });
  const [showContentPage, setShowContentPage] = useState(false);
  const [mainNavData, setMainNavData] = useState(null);
  const [sideNavData, setSideNavData] = useState(null);
  const [sideNavMenuSelectedId, setSideNavMenuSelectedId] = useState(null);
  const [contentNavMenuSelectedId, setContentNavMenuSelectedId] =
    useState(null);

  const display = "d-none";

  return (
    <Container fluid>
      
        <MainNavMenu setSideNavMenuSelectedId={setSideNavMenuSelectedId} />
      
      <Row>
        <Col md={2}>
          {sideNavMenuSelectedId && (
            <SideNavMenu
              SetShowContentPage={setShowContentPage}
              sideNavMenuSelectedId={sideNavMenuSelectedId}
              setContentNavMenuSelectedId={setContentNavMenuSelectedId}
              ShowContentPage={showContentPage}
            />
          )}
        </Col>
        <Col md={10}>
          {contentNavMenuSelectedId && (
            <ContentBlock
              contentNavMenuSelectedId={contentNavMenuSelectedId}
              SetShowContentPage={setShowContentPage}
              ShowContentPage={showContentPage}
            />
          )}
        </Col>
      </Row>
    </Container>
  );
};

export default Pwa;
