import React, { useEffect, useState } from "react";
import { InvokePublication } from "../Services/ServicesFunctions";
import { useRequest } from "ahooks";
import {
  ListGroup,
  Tab,
  Spinner,
  Container,
  Row,
  Col,
  OverlayTrigger,
  Tooltip,
} from "react-bootstrap";
import { ReactComponent as PdfIcon } from "../icons/pdf.svg";
import { ReactComponent as TextIcon } from "../icons/text.svg";

import { ReactComponent as URLIcon } from "../icons/link-hyperlink-url.svg";
import AddContent from "./AddContent";

const ContentBlock = (props) => {
  const [contentPageSelectId, setContentPageSelectId] = useState(null);

  return (
    <>
      {props.ShowContentPage ? (
        <ContentPage
          ContentPageSelectId={contentPageSelectId}
          SetShowContentPage={props.SetShowContentPage}
        />
      ) : (
        <ContentNavMenu
          ContentNavMenuSelectedId={props.contentNavMenuSelectedId}
          SetContentPageSelectId={setContentPageSelectId}
          SetShowContentPage={props.SetShowContentPage}
        />
      )}
    </>
  );
};
const ContentNavMenu = (props) => {
  const { data, error, run, loading } = useRequest(InvokePublication, {
    manual: true,
  });
  useEffect(() => {
    run(props.ContentNavMenuSelectedId, 0);
    return () => {
      console.log("ContentNavMenu unmount");
    };
  }, [props.ContentNavMenuSelectedId]);
  if (loading) {
    return (
      <div className="load-wrapper">
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      </div>
    );
  }
  if (error) {
    return <div>Error</div>;
  }
  if (data) {
    return (
      <Tab.Content>
        {data.map((menuItem) => (
          <ContentNavMenuItem
            SetShowContentPage={props.SetShowContentPage}
            SetContentPageSelectId={props.SetContentPageSelectId}
            key={menuItem.Id}
            MenuItem={menuItem}
          />
        ))}
        <AddContent />
      </Tab.Content>
    );
  }
};
const ContentNavMenuItem = (props) => {
  if (props.MenuItem.ContentType == "text") {
    return (
      <div className="d-flex p-2">
        <span className="material-icons">
          article
          {/* <TextIcon /> */}
        </span>

        <span
          className="content-link"
          onClick={() => {
            props.SetContentPageSelectId(props.MenuItem.Id);
            props.SetShowContentPage(true);
          }}
        >
          {props.MenuItem.TaskName}
        </span>
      </div>
    );
  }
  if (props.MenuItem.ContentType == "url") {
    return (
      <div className="d-flex p-2">
        <p className="material-icons">
          link
        </p>

        <p
          href={props.MenuItem.UrlValue}
          target="_blank"
          className="content-link"
        >
          {props.MenuItem.TaskName}
        </p>
      </div>
    );
  }
  if (props.MenuItem.ContentType == "pdf") {
    return (
      <div className="d-flex p-2">
        <span class="material-icons">picture_as_pdf</span>
        <p className="content-link">{props.MenuItem.TaskName}</p>
      </div>
    );
  }
};
const ContentPage = (props) => {
  const { data, error, run, loading } = useRequest(InvokePublication, {
    manual: true,
  });
  useEffect(() => {
    run(props.ContentPageSelectId, 0);
    return () => {
      console.log("ContentNavMenu unmount");
    };
  }, [props.ContentPageSelectId]);
  if (loading) {
    return (
      <div className="load-wrapper">
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      </div>
    );
  }
  if (error) {
    return <div>eroor</div>;
  }
  if (data) {
    return (
      <Container fluid>
        <Row>
          <div className="d-flex d-flex justify-content-between">
            <div
              onClick={() => props.SetShowContentPage(false)}
              className="m-2"
            >
              <i class="fa-solid fa-xmark"></i>
            </div>
            <div>
              <h2 className="text-center">{data[0].TaskName}</h2>
            </div>
            <div className="m-2">
              <OverlayTrigger
                placement="bottom"
                delay={{ show: 250, hide: 400 }}
                overlay={<Tooltip id="button-tooltip-2">Открыть в 1ф</Tooltip>}
              >
                <a
                  href={
                    "https://ecocomplect.1forma.ru/maintaskform.aspx?TaskID=" +
                    data[0].Id
                  }
                  target="_blank"
                  className="fa-solid fa-up-right-from-square"
                ></a>
              </OverlayTrigger>
            </div>
          </div>
        </Row>
        <Row>
          <div dangerouslySetInnerHTML={{ __html: data[0].HTMLContent }}></div>
        </Row>
      </Container>
    );
  }
};

export default ContentBlock;
